import 'styles.css';

class Dropdown extends React.Component{

    constructor(props){
        super(props);
        
        this.title = this.props.title;
        this.expand = this.expand.bind(this);
        this.state={
            show:false
        }
    }

    expand(){
        this.setState({
            show:!this.state.show
        });
    }

    render(){
        const dropDown = <img width='20px' height='20px' src="/down_arrow.png" onClick={this.expand}/>
        const info = <ul>
            <li>Data Structures</li>
            <li>Basic Algorithms</li>
        </ul> 
        const style = <style jsx>{`
            button{
                width:20px;
                height:20px;
            }
            h3{
                display:inline-block;
            }
        `}</style>      
        console.log(this.state.show && info);  
        return <div className='drop'>
            {style}
            <h3>{this.title}</h3>
            {dropDown}
            {this.state.show && info}
            {/* {info && this.state.show} */}

        </div>
    }
}

export default Dropdown;